import React from 'react';
import './App.css';
import axios from 'axios';
import HeaderComponent from './HeaderComponent';
import InnerComponent from './InnerComponent';

class App extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      ESProblem: [],
      AngularProblem: [],
      ReactProblem: [],
      IsRenderInnerComponent: false,
      InnerComponentData: null
    }
  }
  componentDidMount(){
    this.fetchData();
  }

  fetchData() {
    axios.get("ESProblem.json").then(res => {
      this.setState({
          ESProblem: res.data.ESProblem
      });
    });
    axios.get("AngularProblem.json").then(res => {
      this.setState({
        AngularProblem: res.data.AngularProblem
      });
    });
    axios.get("ReactProblem.json").then(res => {
      this.setState({
        ReactProblem: res.data.ReactProblem
      });
    });
  }

  displayInnerComponent = (item) => {
    this.setState({
      IsRenderInnerComponent: true,
      InnerComponentData: item
    });
  }
  resetInnerComponent = () => {
    this.setState({
      IsRenderInnerComponent: false,
      InnerComponentData: null
    });
  }
  render(){
    return (
      <div className="App">
        <div className="container">
          <HeaderComponent />
        { this.state.IsRenderInnerComponent ?
          <InnerComponent data={this.state.InnerComponentData} reset={this.resetInnerComponent}/> 
          :
          <div>
          <div className="card-container">
            <div className="header">Javascript - ES 5/6</div>
            {
              this.state.ESProblem.map((item, index) => {
                return(
                  <div key={index} className="card-item">
                    <div className="problem">{item.Problem}</div>
                    <div className="desc">{item.Desc}</div>
                    <div className="read-more" onClick={() => this.displayInnerComponent(item)}>Read More</div>
                  </div>
                )
              })
            }
          </div>
          <div className="card-container">
            <div className="header">Angular - 5/6</div>
            {
              this.state.AngularProblem.map((item, index) => {
                return(
                  <div key={index} className="card-item">
                    <div className="problem">{item.Problem}</div>
                    <div className="desc">{item.Desc}</div>
                    <div className="read-more" onClick={() => this.displayInnerComponent(item)}>Read More</div>
                  </div>
                )
              })
            }
          </div>
          <div className="card-container">
            <div className="header">React.js</div>
            {
              this.state.ReactProblem.map((item, index) => {
                return(
                  <div key={index} className="card-item">
                    <div className="problem">{item.Problem}</div>
                    <div className="desc">{item.Desc}</div>
                    <div className="read-more" onClick={() => this.displayInnerComponent(item)}>Read More</div>
                  </div>
                )
              })
            }
          </div>
          </div>
        }
        </div>
      </div>
    )
  };
}

export default App;
