import React from 'react';
import { Button } from 'react-bootstrap';

export default class InnerComponent extends React.Component {
    constructor(props){
        super(props);
    }
    render(){
        return(
            <div className="container">
                <div className="header">{this.props.data.Problem}</div>
                <div className="desc" style={{height:'100%'}}>{this.props.data.Desc}</div>
                {
                    this.props.data.Solution.map((sol, index) => {
                        return (
                            <div key={index} style={{margin: '1rem'}}>
                                <div style={{fontWeight:'bold'}}><i>{sol.Header}</i></div>
                                <code style={{marginLeft:'1rem'}}>{sol.code}</code>
                            </div>
                        )
                    })
                }
                <div style={{margin: '1rem'}}>
                    <span style={{fontWeight:'bold'}}><i>Input: &nbsp;</i></span><span><code>{this.props.data.Input}</code></span>
                </div>
                <div style={{margin: '1rem'}}>
                    <span style={{fontWeight:'bold'}}><i>Output: &nbsp;</i></span><span><code>{this.props.data.Output}</code></span>
                </div>
                <br></br>
                <div style={{textAlign:'center'}}>
                    <Button variant="outline-dark" onClick={this.props.reset}>Back</Button>
                </div>
                <br></br>
            </div>
        )
    }
}