import React from 'react';
import { Navbar } from 'react-bootstrap';

export default class HeaderComponent extends React.Component {
    render() {
        return(
            <div align="center">
            <Navbar bg="dark" variant="dark">
                <Navbar.Brand onClick={() => window.location.reload()}>
                    <div style={{display:'flex',alignItems:'center'}}>
                    <div style={{width:'15rem'}}><img src="./logo.svg" /></div>
                    <div align="center">
                        <div style={{fontSize:'2em',fontFamily:'cursive'}}>
                            <span style={{color:'#0f90fe'}}>Javascript </span>
                            <span style={{color:'#0ffe90'}}>Tricks</span>
                        </div>
                    </div>
                    </div>
                </Navbar.Brand>
            </Navbar>
            </div>
        )
    }
}